import { useState } from "react"
import Link from "next/link"

const Index = () => {

    const [users, setUsers] = useState([
        {id: 1, name: "Ivelin"},
        {id: 2, name: "Ivan"},
    ])

    return (
        <div>
            <h1>Users Page</h1>
            <ul>
                {users.map(user => 
                    <Link href={`/users/${user.id}`}>
                        {user.name}
                    </Link>
                )}
            </ul>
        </div>
    )
}

export default Index